.Tooltip {
  background-color: #ecf0f5;
  border-radius: 0.25rem;
  color: #34495E;
  font-family: "Nunito Sans", "Roboto", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  padding: 0.75rem 1rem;
  position: absolute;
  left: 100%;
  top: 0;
  transition: background-color 0.15s ease-out, color 0.15s ease-out, opacity 0.15s ease-out, box-shadow 0.15s ease-out;
  white-space: nowrap; }
  .Tooltip:after {
    background-color: inherit;
    border-radius: 0.125rem;
    content: '';
    height: 1rem;
    position: absolute;
    left: -0.375rem;
    top: 0.75rem;
    width: 1rem;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg); }
  .Tooltip.tooltip-discloseable {
    opacity: 0; }

*:hover > .tooltip-discloseable,
*:focus > .tooltip-discloseable {
